/*Voor responsiviteit*/
#svg-container { 
  position: relative;
  width: 100%;
  height: 0;
  margin: 15px 0;
  padding-top: 58.333%;
}
svg {
  position: absolute;
  top: 0;
  left:  0;
  background-color: #fff;
  margin-bottom: -5px; /* voorkomt smalle witte padding onder*/
}
.placenames-layer0, .placenames-layer1 {
  text-anchor: middle;
  fill: #333;
  font-family: sans-serif;
  font-size: 9.2px;
  font-weight: bold;
}
.placenames-layer0 {
  stroke: #fff;
  stroke-width: 1.7;
}
path.domain, .tick line {
  fill: none;
  stroke: #333;
  stroke-width: 0.5px;
  shape-rendering: crispEdges;
}
#legend-g text {
    font-family: sans-serif;
    font-size: 9px;
    fill: #000;
}
.rivernames text {
    fill: rgb(124,181,218);
    stroke: none;
    stroke-width: 0;
    font-size: 11px;
    font-family: sans-serif;
    font-style: italic;
}
#buttons-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
  justify-content: space-between;
}
#water-buttons-container {
  -webkit-order: 2;
  order:  2;
}
#zoom-button-container {
  -webkit-order: 1;
  order: 1;
}
#low-water-button, #high-water-button,
#low-water-button:hover, #high-water-button:hover {
  background-color: #7CB5DA !important;
}
#low-water-button.disabled, #high-water-button.disabled,
#low-water-button.zooming, #high-water-button.zooming {
  opacity: 0.3;
  pointer-events: none;
}
#low-water-button {
  margin-left: 8px;
}
@media (max-width: 420px) {
  #low-water-button, #high-water-button, #zoom-button {
    padding: 3px 6px;
  }
}